#headerArea {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  padding: 35px clamp(1.25rem, 1.0378rem + 0.9302vw, 1.875rem);
  width: 100vw;
  top: 0;
  left: 0;
  transition: 0.7s;
  z-index: 1000;
}
#headerArea.scroll {
  padding-top: 21px;
  padding-bottom: 21px;
  background-color: #21054d;
}

#menuBtn {
  display: none;
}
@media screen and (max-width: 768px) {
  #menuBtn {
    display: block;
    position: relative;
    width: 34px;
    height: 34px;
    z-index: 1000;
  }
  #menuBtn span, #menuBtn::before, #menuBtn::after {
    content: "";
    display: block;
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #fff;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    transition: 0.3s;
  }
  #menuBtn::before {
    transform: translateY(calc(-50% - 5px));
  }
  #menuBtn::after {
    transform: translateY(calc(-50% + 5px));
  }
  #menuBtn.act span {
    opacity: 0;
  }
  #menuBtn.act::before {
    transform: translateY(-50%) rotate(45deg);
  }
  #menuBtn.act::after {
    transform: translateY(-50%) rotate(-45deg);
  }
}

